.calendar {
  display: flex;
  flex-wrap: wrap;
  gap: $block-gap;

  padding: 10px;

  width: (7 * $block-size) + (6 * $block-gap);

  list-style: none;

  &__block {
    width: $block-size;
    height: $block-size;

    background-color: $main;

    box-sizing: border-box;
    border: $block-border-width solid $border;
    transition: all 0.5s ease-in-out;

    position: relative;

    &::before {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    @for $date from 1 through 31 {
      &:nth-child(#{$date})::before {
        content: '#{$date}';
      }
    }

    @each $weekday, $weekday-id in $weekdays {
      .calendar--start-day-#{$weekday} &:first-child {
        margin-left: $weekday-id * ($block-size + $block-gap);
      }
    }

    @for $days-in-month from 28 through 31 {
      .calendar--month-length-#{$days-in-month}
        &:nth-child(n + #{$days-in-month + 1}) {
        display: none;
      }
    }

    &:hover {
      cursor: pointer;
      background-color: $hover;
      transform: translateY(-20px);
    }
  }
}
